@media (max-width: 768px) {
    .App-section {
        opacity: 1 !important;
        transform: none !important;
    }
    .App-header nav {
        display: flex; 
        flex-wrap: wrap; 
        justify-content: center; 
        padding: 10px; 
    }

    .App-header nav a {
        display: none;
    }

    .App-sidebar {
        width: 75%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .App-info {
        padding: 10px 0;
        margin-bottom: 10px;
        width: 100%;
    }

    .App-main {
        display: flex; 
        flex-direction: column; 
        align-items: center;
    }

    .App-picture {
        margin-bottom: 20px;
    }

    .App-content {
        width: 100%;
        padding: 0 20px;
        text-align: center;
    }

    .About-content {
        text-align: center;
        padding: 0 20px;
    }

    .Skills-content { 
        width: 100%;
        text-align: center;
    }

    .Skills-grid {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .Skills-section {
        width: 90%;
        margin-bottom: 20px; 
        padding: 10px;
    }

    .Skills-Project {
        width: 90%;
        margin-top: 10px;
        margin-bottom: 20px;
        padding: 10px;
    }

    .Education-content {
        text-align: center;
    }

    .Education-details {
        text-align: center;
        margin: 0 auto;
        display: inline-block;
        width: 100%;
    }

    .Education-section {
        width: 100%;
    }

    .Education-cert {
        list-style: none;
        padding: 0;
        margin: 0 auto;
        display: inline-block;
        text-align: center;
        width: 100%;
    }
    .Education-cert li {
        margin-bottom: 10px;
    }

    .Projects-content {
        display: none;
    }
  

  }